/* external node css */
.react-flow__node-graphNode {
    border-radius: var(--node-border-radius);
    display: flex;
    min-width: 150px;
    font-family: 'Fira Mono', Monospace;
    font-weight: 500;
    letter-spacing: -0.2px;
}
.node-box-shadow-dark{
	box-shadow: var(--node-box-shadow);
}
.node-box-shadow-neo{
	box-shadow: 10px 0 15px rgba(41, 245, 0, 0.3), 
						 -10px 0 15px rgba(21, 255, 0, 0.616);
}
.node-box-shadow-light{
	box-shadow: 10px 0 15px rgba(34, 34, 34, 0.3), 
						 -10px 0 15px rgba(102, 102, 102, 0.616);
}
.react-flow__node-graphNode .wrapper {
	overflow: hidden;
	display: flex;
	padding: 2px;
	position: relative;
	border-radius: var(--node-border-radius);
	flex-grow: 1;
	height:100%;
}


/* gradients on */
.gradient-dark-on:before {
	content: '';
	position: absolute;
	padding-bottom: calc(100% * 1.41421356237);
	width: calc(100% * 1.41421356237);
	background: conic-gradient(
		from -160deg at 50% 50%,
		#e92a67 0deg,
		#a853ba 120deg,
		#2a8af6 240deg,
		#e92a67 360deg
	);
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	border-radius: 100%;
}
.react-flow__node-graphNode.selected .wrapper.gradient-dark-on:before {
	content: '';
	background: conic-gradient(
		from -160deg at 50% 50%,
		#e92a67 0deg,
		#a853ba 120deg,
		#2a8af6 240deg,
		rgba(42, 138, 246, 0) 360deg
	);
	animation: spinner 4s linear infinite;
	transform: translate(-50%, -50%) rotate(0deg);
	z-index: -1;
}
@keyframes spinner {
	100% {
		transform: translate(-50%, -50%) rotate(-360deg);
	}
}

