/* inner switches for themes */
.react-flow__node-graphNode .inner {
	padding: 3px 10px;
	border-radius: var(--node-border-radius);
	display: flex;
	flex-direction: column;
	justify-content: center;
	flex-grow: 1;
	position: relative;
	overflow-wrap: anywhere;
}
.react-flow__node-graphNode .light{
	background: rgb(241, 227, 227);
	color:black;
}
.react-flow__node-graphNode .dark{
	background: rgb(17, 17, 17);
	color: #F4C361;
}
.react-flow__node-graphNode .neo{
	background: rgb(17, 17, 17);
	color: #00c431;
}

.react-flow__node-graphNode .inner:after {
	content: "";
	position:absolute;
	right:4px;
	bottom: 4px;
	width:4px;
	height:4px;
	transform:rotate(-45deg);
	background:white;
	border-radius:50%;
	opacity:70%;
}
.react-flow__node-graphNode .inner:after:hover {
	opacity:100%;
}
.react-flow__node-graphNode .body {
	display: flex;
	height: 90%;
	width: 100%;
	overflow-wrap: anywhere;
}
.react-flow__node:focus {
	outline: none;
}